<template>
    <v-card
            outlined
            max-width="500px"
    >

<!--        <v-list-item>-->
<!--            <v-list-item-content>-->
<!--                <v-row align="center" justify="end">-->
<!--                    <v-btn text @click.native="removeMainRow(value,viewId)">-->
<!--                        <v-icon small>mdi-minus-circle-outline</v-icon>-->
<!--                    </v-btn>-->
<!--                </v-row>-->
<!--            </v-list-item-content>-->
<!--        </v-list-item>-->

        <v-row align="center" justify="end">
            <v-btn text  v-if="index != 0"  @click.native="removeMainRow(index)">
                <v-icon small>mdi-minus-circle-outline</v-icon>
            </v-btn>
        </v-row>

        <v-list-item>
            <v-list-item-content>
                <v-list-item-subtitle>

                    <v-col align="start" justify="center">

                        <div style="font-size: 15px">Delete view table</div>
                        <v-row align="center" justify="center" style=" margin-left: 30px">
                            <v-autocomplete
                                    v-model="deleteItem.sourceEvent"
                                    :items="events"
                                    item-text="name"
                                    return-object
                                    prefix="WHEN"
                                    style="font-size: 15px"
                            ></v-autocomplete>
                        </v-row>


                        <div style="font-size: 15px; margin-left: 30px;">WHERE</div>
                        <v-simple-table
                                dense style="margin-left: 30px;"
                        >
                            <template v-slot:default>
                                <thead>
                                <tr>

                                </tr>
                                </thead>
                                <tbody>
                                <tr v-for="(item,key) in deleteItem.condition">

                                    <td class="text-right" v-if="key == deleteItem.condition.length-1">
                                        <v-autocomplete
                                                v-model="item.conditionViewField"
                                                :items="value.fieldDescriptors"
                                                item-text="name"
                                                return-object
                                                label="viewField"
                                        ></v-autocomplete>
                                    </td>
                                    <td class="text-right" v-else> {{ value.name }}.{{ item.conditionViewField.name }}</td>

                                    <td class="text-center"> = </td>

                                    <td class="text-left" v-if="key == deleteItem.condition.length-1">
                                        <v-autocomplete
                                                v-model="item.conditionEventField"
                                                :items="deleteItem.sourceEvent.fieldDescriptors"
                                                item-text="name"
                                                return-object
                                                label="eventField"
                                        ></v-autocomplete>
                                    </td>
                                    <td class="text-right" v-else> {{ deleteItem.sourceEvent.name }}.{{ item.conditionEventField.name }}</td>

                                    <td class="text-center">
                                        <v-btn small text
                                               @click.native="removeConditionRow(deleteItem.condition,key)">
                                            <v-icon small> delete </v-icon>
                                        </v-btn>
                                    </td>
                                </tr>
                                </tbody>
                            </template>
                        </v-simple-table>


                        <v-row align="center" justify="end" style="margin-left: 30px;">
                            <v-btn text small   @click.native="addConditionRow(deleteItem.condition)">
                                <v-icon small> mdi-plus</v-icon>
                            </v-btn>
                        </v-row>

                    </v-col>

                </v-list-item-subtitle>
            </v-list-item-content>
        </v-list-item>

        <v-card-actions>

        </v-card-actions>
    </v-card>
</template>

<script>
    export default {
        name: 'ViewDelete',
        props: {
            value: Object,
            deleteItem: Object,
            events: Array,
            index: Number,
        },
        data: function () {
            return {
                saveBtn:false
            }
        },
        methods: {
            removeMainRow(viewId){
                var me = this

                me.value.viewFieldDescriptors.deleteFieldDescriptors[viewId] = null
                me.value.viewFieldDescriptors.deleteFieldDescriptors = me.value.viewFieldDescriptors.deleteFieldDescriptors.filter(n => n)

            },
            removeConditionRow: function (table, removeKey) {
                var me = this
                table.some(function (views, idx) {
                    if (idx == removeKey) {
                        if (idx > -1) {
                            table.splice(idx, 1)
                        }
                    }
                })
            },
            addConditionRow: function (table) {
                var me = this

                if (table.length !=0 && ( Object.keys(table[table.length - 1].conditionViewField).length === 0 || Object.keys(table[table.length - 1].conditionEventField).length === 0) ) {
                    var designer = this.getComponent('modeling-designer')
                    designer.text = "TextField input Text"
                    designer.snackbar = true
                } else {
                    var obj = {
                        conditionViewField: {},
                        conditionEventField: {}
                    }
                    table.push(obj)
                }

            },
            getComponent(componentName) {
                let component = null
                let parent = this.$parent
                while (parent && !component) {
                    if (parent.$options.name === componentName) {
                        component = parent
                    }
                    parent = parent.$parent
                }
                return component
            },
        }
    }
</script>