<template>

    <v-container>
        <div>
            <v-divider></v-divider>
        </div>
        <v-row>
            <v-col
                    v-for="(item, key) in filteredVersionList"
                    :key="key"
                    cols="6"
                    md="4"
            >
                <v-card
                        outlined
                        class="mx-auto"
                        max-width="400"
                        max-height="400"
                >
                    <v-img
                            :src="item.versionImg"
                            max-width="400"
                            max-height="300"

                    ></v-img>
                    <v-list-item three-line>
                        <v-list-item-content>
                            <v-list-item-title class="headline mb-1">Ver.{{ item.versionNum }}
                                <div style="font-size:15px; float: right">{{item.versionName}}</div>
                            </v-list-item-title>
                            <v-list-item-subtitle>Modified Date : {{ item.versionDate }}</v-list-item-subtitle>
                            <v-list-item-subtitle v-if=" `${item.versionType}` !='0' ">Comment : {{ item.versionComment }}</v-list-item-subtitle>
                        </v-list-item-content>
                    </v-list-item>
                    <v-card-actions>
                        <!-- toUrl(projectName, author, verName, type) -->
                        <v-btn text
                        @click="versionModalShow(item.versionValue)">
                            View
                        </v-btn>

                    </v-card-actions>
                </v-card>

            </v-col>
            <v-col
                    cols="6"
                    md="4"
            >
                <v-card
                        class="mx-auto"
                        max-width="344"
                        align="center"
                >
                </v-card>
            </v-col>
        </v-row>

        <v-layout>
            <modal name="view-modeling-designer-modal" :height='"80%"' :width="'80%'">
                <view-modeling-designer :versionValue="versionValue"></view-modeling-designer>
            </modal>
        </v-layout>


    </v-container>
</template>

<script>
    import firebase from 'firebase';

    export default {
        name: "ViewEventStormingVersionPage",
        props: {},
        data() {
            return {
                versionList: [],
                name: '',
                search: '',
                loginAuthor: '',
                onLogin: false,
                // 
                deleteDialog: false,
                selectDelete: [],
                checkAllVersion: false,
                versionValue:{}
            }
        },
        created() {
            var me = this;

            // if (window.localStorage.getItem("accessToken") != null) {
                me.getVersionList()
                me.$nextTick(function () {
                    me.filteredVersionList
                })
            // }
        },
        computed: {
            filteredVersionList() {
                var me = this

                if (me.search) {
                    return me.versionList.filter(eventstorming => {
                        return eventstorming.name.toLowerCase().includes(me.search.toLowerCase())
                    })
                } else {
                    if (me.versionList)
                        return me.versionList
                }
            }
        },
        mounted: function () {
            var me = this

            // me.$EventBus.$on('login', function (newVal) {
            //     // console.log("----------------------")
            //     me.getVersionList()
            // })

        },
        methods: {
            versionModalShow(value) {
                this.versionValue = value
                this.$modal.show('view-modeling-designer-modal')
            },
            getVersionList() {
                var me = this
                var getAuthor = this.$route.params.author;
                var getProject = this.$route.params.projectName;
                var getVersion = this.$route.params.version;
                var getType = this.$route.params.type;
                var list = []
                var initValue = {
                    'definition': [],
                    'relation': []
                }
                var versionNum = 1;

                me.versionList = []
                if (getType == 'true') {
                    firebase.database().ref("/saver/shared/" + getAuthor + "/" + getProject).once('value').then(function (snapshot) {
                        // console.log(snapshot.val())
                        for (var name in snapshot.val()) {
                            var ob = {
                                versionNum: versionNum++,
                                versionName: name,
                                versionDate: snapshot.val()[name].date,
                                versionComment: snapshot.val()[name].comment,
                                versionEmail: snapshot.val()[name].email,
                                versionImg: snapshot.val()[name].img,
                                versionValue: snapshot.val()[name].value,
                                versionType: getType,
                            }
                            list.push(ob)
                        }
                    })
                } else if (getType == 'false') {
                    firebase.database().ref("/saver/mine/" + getAuthor + "/" + getProject).once('value').then(function (snapshot) {
                        for (var name in snapshot.val()) {
                            var ob = {
                                versionNum: versionNum++,
                                versionName: name,
                                versionDate: snapshot.val()[name].date,
                                versionComment: snapshot.val()[name].comment,
                                versionEmail: snapshot.val()[name].email,
                                versionImg: snapshot.val()[name].img,
                                versionValue: snapshot.val()[name].value,
                                versionType: getType,
                            }
                            list.push(ob)
                        }
                    })
                }else if(getType == '0'){
                    var ob = {
                        versionNum: 1,
                        versionName: localStorage.getItem("localSaveProjectName") != null? localStorage.getItem("localSaveProjectName"):'Local',
                        versionDate: localStorage.getItem("localSaveTime") != null? localStorage.getItem("localSaveTime"): 'undefined',
                        versionComment: '',
                        versionEmail: localStorage.getItem("email") != null? localStorage.getItem("email"):"anonymous",
                        versionImg: 'https://github.com/sooheon45/eventstorming/blob/master/docs/media/evtstrm.jpg?raw=true',
                        versionType: "0",
                        versionValue:localStorage.getItem('localLoadData') != null?localStorage.getItem('localLoadData'):initValue
                    }
                    list.push(ob)
                }
                me.versionList = list

            },
            toUrl(projectName, author, verName, type) {
                return "storming/" + author + "/" + projectName + "/" + verName + "/" + type
            },
            getUid() {
                return new Promise(function (resolve) {
                    firebase.auth().onAuthStateChanged(function (user) {
                        if (user) {
                            resolve(user.uid);
                        } else {
                            resolve("everyone")
                        }
                    });
                })
            },
        }
    }
</script>

