<template>
    <div>
        <geometry-element
                selectable
                movable
                resizable
                deletable
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                :angle.sync="value.elementView.angle"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:dblclick="showProperty"
                v-on:addedToGroup="onAddedToGroup"
                v-on:removeShape="onRemoveShape(value)"
                :label="value.name"
                :_style="{
                'label-angle':value.elementView.angle,
                'font-weight': 'bold','font-size': '16'
                }"
        >
            <!--v-on:dblclick="$refs['dialog'].open()"-->
            <geometry-rect
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#F8D454',
          fill: '#F8D454',
          'fill-opacity': 1,
          r: '1'
        }"
            >
            </geometry-rect>
            <sub-elements>
                <!--title-->
                <text-element
                        :sub-width="'100%'"
                        :sub-height="titleH"
                        :sub-top="0"
                        :sub-left="0"
                        :text="value.classReference ? value.classReference : '<< Aggregate >>'">
                </text-element>
                <image-element
                        v-if="(Object.keys(value.boundedContext).length === 0 && JSON.stringify(value.boundedContext) === JSON.stringify({})) || value.name.length < 1"
                        v-bind:image="value.alertURL"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="'5px'"
                        :sub-bottom="'5px'"
                >
                </image-element>
            </sub-elements>
        </geometry-element>


        <modeling-property-panel
                v-model="value"
                :titleName="'Aggregate'"
                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/aggregate.png'"
        >
        </modeling-property-panel>

    </div>
</template>

<script>
    import Element from '../../modeling/Element'

    // var Mustache = require('mustache')

    export default {
        mixins: [Element],
        name: 'aggregate-definition',
        props: {},
        computed: {
            namePascalCase() {
                var me = this
                return me.name.charAt(0).toUpperCase() + me.name.slice(1)
            },
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.Aggregate'
            },
            createNew(elementId, x, y, width, height) {
                return {
                    _type: this.className(),
                    name: '',
                    namePlural: '',
                    namePascalCase: '',
                    nameCamelCase: '',
                    alertURL: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': 100,
                        'height': 100,
                        'style': JSON.stringify({})
                    },
                    description_text: '     \n' +
                        '(      value objects )',
                    checkValue: false,
                    drawer: false,
                    selected: false,
                    boundedContext: {},
                    aggregateRoot: {
                        _type: "org.uengine.modeling.model.AggregateRoot",
                        fieldDescriptors:
                            [
                                {
                                    _type: "org.uengine.model.FieldDescriptor",
                                    name: "id",
                                    className: "Long",
                                    nameCamelCase: 'id',
                                    namePascalCase: 'Id',
                                    isKey: true
                                }
                            ],
                    },
                    events: [],
                    commands: [],
                    viewes: [],
                }

            }
        },
        data: function () {
            return {
                itemH: 200,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,

            };
        },
        created: function () {

        },
        watch: {
            "value.name": function (newVal) {
                var me = this
                me.value.checkValue = true
            },
            "value.aggregateRoot.fieldDescriptors": function () {
                var me = this
                console.log("change")
                me.value.checkValue = true
            },
            "value.drawer":  _.debounce(
                function (newVal) {
                var me = this
                var designer = this.getComponent('modeling-designer')
                console.log(newVal)
                if (newVal == false) {
                    if (me.value.checkValue) {
                        var obj = {
                            state: 'updateAggregate',
                            element: JSON.parse(JSON.stringify(me.value))
                        }
                        //command, event, policy 

                        if( me.value.boundedContext.id != null){
                            me.$EventBus.$emit(me.value.boundedContext.id, obj)
                        }

                        if (me.value.events.length > 0)
                            me.value.events.forEach(function (eve) {
                                console.log("emit eve")
                                me.$EventBus.$emit(`${eve.elementView.id}`, obj)
                                // eve.aggregate = tmpAgg
                                // eve.fieldDescriptors = me.value.aggregateRoot.fieldDescriptors
                            })

                        if (me.value.commands.length > 0)
                            me.value.commands.forEach(function (co) {
                                me.$EventBus.$emit(`${co.elementView.id}`, obj)
                                // co.aggregate = tmpAgg
                            })

                        // if (me.value.policies.length > 0)
                        //     me.value.policies.forEach(function (po) {
                        //         me.$EventBus.$emit(`${po.elementView.id}`, obj)
                        //         // po.aggregate = tmpAgg
                        //     })

                        if (me.value.viewes.length > 0)
                            me.value.viewes.forEach(function (vi) {
                                me.$EventBus.$emit(`${vi.elementView.id}`, obj)
                                // vi.aggregate = tmpAgg
                            })
                        me.value.checkValue = false
                    }
                }
            },200)

        },
        mounted: function () {
            var me = this

            this.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {
                if(obj.state.includes('Bounded')) {
                    if(obj.state == 'updateBounded') {
                        me.value.boundedContext.name = obj.element.name;
                        me.value.boundedContext.id = obj.element.elementView.id;
                        if (me.value.events.length > 0)
                            me.value.events.forEach(function (eve) {
                                eve.boundedContext.name = obj.element.name;
                                eve.boundedContext.id = obj.element.elementView.id;
                            })

                        if (me.value.commands.length > 0)
                            me.value.commands.forEach(function (co) {
                                // me.$EventBus.$emit(`${co.elementView.id}`, obj)
                                // co.aggregate = tmpAgg
                                co.boundedContext.name = obj.element.name;
                                co.boundedContext.id = obj.element.elementView.id;
                            })

                        // if (me.value.policies.length > 0)
                        //     me.value.policies.forEach(function (po) {
                        //         // me.$EventBus.$emit(`${po.elementView.id}`, obj)
                        //         // po.aggregate = tmpAgg
                        //         po.boundedContext.name = obj.element.name;
                        //         po.boundedContext.id = obj.element.elementView.id;
                        //     })

                        if (me.value.viewes.length > 0)
                            me.value.viewes.forEach(function (vi) {
                                me.$EventBus.$emit(`${vi.elementView.id}`, obj)
                                vi.boundedContext.name = obj.element.name;
                                vi.boundedContext.id = obj.element.elementView.id;
                                // vi.aggregate = tmpAgg
                            })

                        var aggObj = {
                            state: "updateAggregate",
                            element: JSON.parse(JSON.stringify(me.value))
                        }
                        me.$EventBus.$emit(me.value.boundedContext.id, aggObj)
                    }
                    if(obj.state == 'deleteBounded') {

                    }

                } else {
                    me.managedAgg(obj).then(function (resolve) {
                        if (me.value.boundedContext != null && me.value.boundedContext.id != null) {
                            var aggObj = {
                                state: "updateAggregate",
                                element: JSON.parse(JSON.stringify(me.value))
                            }
                            me.$EventBus.$emit(me.value.boundedContext.id, aggObj)
                        }
                    })
                }
            })
        },
        methods: {
            managedAgg(obj) {
                var me = this
                return new Promise(function (resolve) {
                    if (obj.state == 'deleteEvent') {
                        // console.log(me.value)
                        me.value.events.some(function (item, idx) {
                            if (item.elementView.id == obj.element.elementView.id) {
                                console.log(me.value.events[idx])
                                me.value.events[idx] = null;
                                me.value.events = me.value.events.filter(n => n)
                                resolve()
                            }
                        })
                    }
                    if (obj.state == 'addEvent') {
                        me.value.events.push(obj.element)
                        resolve()
                    }
                    if (obj.state == 'updateEvent') {
                        // console.log("aa")
                        me.value.events.some(function (item, idx) {
                            if (item.elementView.id == obj.element.elementView.id) {
                                me.value.events[idx] = obj.element;
                                resolve()
                            }
                        })
                    }
                    // Event 
                    // Policy 
                    // if (obj.state == 'deletePolicy') {
                    //     // console.log(me.value)
                    //     me.value.policies.some(function (item, idx) {
                    //         if (item.elementView.id == obj.element.elementView.id) {
                    //             me.value.policies[idx] = null;
                    //             me.value.policies = me.value.policies.filter(n => n)
                    //             resolve()
                    //         }
                    //     })
                    // }
                    // if (obj.state == 'addPolicy') {
                    //     me.value.policies.push(obj.element)
                    //     resolve()
                    // }
                    // if (obj.state == 'updatePolicy') {
                    //     me.value.policies.some(function (item, idx) {
                    //         if (item.elementView.id == obj.element.elementView.id) {
                    //             me.value.policies[idx] = obj.element;
                    //             resolve()
                    //         }
                    //     })
                    // }
                    // Policy 
                    // Command 
                    if (obj.state == 'deleteCommand') {
                        // console.log(me.value)
                        me.value.commands.some(function (item, idx) {
                            if (item.elementView.id == obj.element.elementView.id) {
                                me.value.commands[idx] = null;
                                me.value.commands = me.value.commands.filter(n => n)
                                resolve()
                            }
                        })
                    }
                    if (obj.state == 'addCommand') {
                        me.value.commands.push(obj.element)
                        resolve()
                    }
                    if (obj.state == 'updateCommand') {
                        me.value.commands.some(function (item, idx) {
                            if (item.elementView.id == obj.element.elementView.id) {
                                me.value.commands[idx] = obj.element;
                                resolve()
                            }
                        })
                    }
                    // Command 
                    // View 
                    if (obj.state == 'deleteView') {
                        // console.log(me.value)
                        me.value.viewes.some(function (item, idx) {
                            if (item.elementView.id == obj.element.elementView.id) {
                                me.value.viewes[idx] = null;
                                me.value.viewes = me.value.viewes.filter(n => n)
                                resolve()
                            }
                        })
                    }
                    if (obj.state == 'addView') {
                        me.value.viewes.push(obj.element)
                        resolve()
                    }
                    if (obj.state == 'updateView') {
                        me.value.viewes.some(function (item, idx) {
                            if (item.elementView.id == obj.element.elementView.id) {
                                me.value.viewes[idx] = obj.element;
                                resolve()
                            }
                        })
                    }
                    resolve()
                })
            },
            deleteAggregate() {
                var me = this
                return new Promise(function (resolve) {
                    // console.log("deleteAggregate")

                    var obj = {
                        state: 'deleteAggregate',
                        element: JSON.parse(JSON.stringify(me.value))
                    }

                    if (me.value.boundedContext.id != null)
                        me.$EventBus.$emit(`${me.value.boundedContext.id}`, obj)

                    if (me.value.events.length > 0)
                        me.value.events.forEach(function (ev) {
                            me.$EventBus.$emit(`${ev.elementView.id}`, obj)
                        })

                    if (me.value.commands.length > 0)
                        me.value.commands.forEach(function (co) {
                            me.$EventBus.$emit(`${co.elementView.id}`, obj)
                        })

                    if (me.value.viewes.length > 0)
                        me.value.viewes.forEach(function (vi) {
                            me.$EventBus.$emit(`${vi.elementView.id}`, obj)
                        })

                    me.$EventBus.$off(`${me.value.elementView.id}`);
                    me.$emit('update:value', null);
                    me.$EventBus.$emit('storage')
                    resolve()
                })
            }
        }
        ,
        beforeDestroy() {
        }

    }
</script>


<style scoped lang="scss" rel="stylesheet/scss">

</style>
