<template>
    <div>
        <geometry-element
                selectable
                :movable="!value.editing"
                :resizable="!value.editing"
                connectable
                deletable
                :angle.sync="value.elementView.angle"
                :id.sync="value.elementView.id"
                :x.sync="value.elementView.x"
                :y.sync="value.elementView.y"
                :width.sync="value.elementView.width"
                :height.sync="value.elementView.height"
                v-on:dblclick="showProperty"
                v-on:selectShape="selectedActivity"
                v-on:deSelectShape="deSelectedActivity"
                v-on:addedToGroup="onAddedToGroup"
                v-on:removeShape="onRemoveShape(value)"
                :label="value.name"
                :_style="{
                'label-angle':value.elementView.angle,
                'font-weight': 'bold','font-size': '16'
                }"
        >
            <!--v-on:dblclick="$refs['dialog'].open()"-->
            <geometry-rect
                    :_style="{
          'fill-r': 1,
          'fill-cx': .1,
          'fill-cy': .1,
          'stroke-width': 1.4,
          'stroke': '#BB94BF',
          fill: '#BB94BF',
          'fill-opacity': 1,
          r: '1'
        }"
            >
            </geometry-rect>

            <sub-elements>
                <!--title-->
                <image-element
                        v-if="value.boundedContext.id !=null || value.name.length <1"
                        v-bind:image="value.alertURL"
                        :sub-width="'24px'"
                        :sub-height="'24px'"
                        :sub-right="'5px'"
                        :sub-bottom="'5px'"
                >
                </image-element>
                <text-element
                        :sub-width="'100%'"
                        :sub-height="titleH"
                        :sub-top="0"
                        :sub-left="0"
                        :text="value.classReference ? value.classReference : '<< Policy >>'">
                </text-element>

            </sub-elements>
        </geometry-element>


        <modeling-property-panel
                v-model="value"
                titleName="Policy"
                :img="'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/event/policy.png'"
        >
        </modeling-property-panel>

    </div>
</template>

<script>
    import Element from '../../modeling/Element'

    var Mustache = require('mustache')

    export default {
        mixins: [Element],
        name: 'policy-definition',
        props: {},
        computed: {
            namePascalCase() {
                var me = this
                return me.name.charAt(0).toUpperCase() + me.name.slice(1)
            },
            defaultStyle() {
                return {}
            },
            className() {
                return 'org.uengine.modeling.model.Policy'
            },
            createNew(elementId, x, y, width, height) {

                return {
                    description_text: '\n' +
                        '   (subscribe)\n',
                    _type: this.className(),
                    name: '',
                    namePascalCase: '',
                    nameCamelCase: '',
                    namePlural: '',
                    aggregate: {},
                    boundedContext: {},
                    alertURL: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
                    elementView: {
                        '_type': this.className(),
                        'id': elementId,
                        'x': x,
                        'y': y,
                        'width': width,
                        'height': height,
                        'style': JSON.stringify({})
                    },
                    checkValue: false,
                    drawer: false,
                    selected: false,
                    editing: false,
                    relationEventInfo: [],
                }
            }
        },
        data: function () {
            return {
                itemH: 20,
                titleH: (this.value.classReference ? 60 : 30),
                reference: this.value.classReference != null,
                referenceClassName: this.value.classReference,
                failed_image: location.pathname + ((location.pathname == '/' || location.pathname.lastIndexOf('/') > 0) ? '' : '/') + 'static/image/symbol/alert-icon.png',
            };
        },
        created: function () {

        },
        watch: {
            "value.relationEventInfo":{
              deep:true,
              handler :function () {
                  var me = this;

                  this.value.checkValue = true
              }
            },
            "value.name": function () {
                this.value.checkValue = true
            },
            "value.boundedContext": {
                deep: true,
                handler: function (newVal, oldVal) {
                    var me= this
                    if(newVal.id == oldVal.id) {
                        // console.log("Equal")
                    } else {
                        // console.log("no Equal")
                        this.value.checkValue = true
                        var addObj = {
                            state: "addPolicy",
                            element: me.value
                        }
                        var deleteObj = {
                            state: "deletePolicy",
                            element: me.value
                        }
                        if (oldVal.id != null ) {
                            me.$EventBus.$emit(oldVal.id, deleteObj)
                        }
                        if(newVal.id != undefined) {
                            me.$EventBus.$emit(newVal.id, addObj)
                        }

                    }
                }
            },
            "value.drawer": _.debounce(
                function (newVal, oldVal) {
                    var me = this
                    if (newVal == false) {
                        var obj = {
                            state: "updatePolicy",
                            element: JSON.parse(JSON.stringify(me.value))
                        }
                        me.$EventBus.$emit(`${me.value.boundedContext.id}`, obj)

                        this.value.checkValue = false
                    }
                }, 200)

        },
        mounted: function () {
            var me = this
            this.$EventBus.$on(`${me.value.elementView.id}`, function (obj) {

                if(obj.state == 'addRelation') {
                    // console.log(obj.element)
                    var checked = false;
                    me.value.relationEventInfo.some(function (eventItem, evtIdx) {
                        var evObj = {
                            relationId: obj.element.relationView.id,
                            relationType: obj.element.relationValue,
                            eventValue: JSON.parse(JSON.stringify(obj.element.sourceElement))
                        }
                        if(eventItem.eventValue.elementView.id == obj.element.sourceElement.elementView.id) {
                            me.value.relationEventInfo[evtIdx] = evObj;
                            checked = true;
                        }
                    })

                    if(!checked) {
                        var evObj = {
                            relationId: obj.element.relationView.id,
                            relationType: obj.element.relationValue,
                            eventValue: JSON.parse(JSON.stringify(obj.element.sourceElement))
                        }
                        me.value.relationEventInfo.push(evObj)
                    }

                    if(me.value.boundedContext.id != null){
                        me.$EventBus.$emit(me.value.boundedContext.id,  {state: "updatePolicy", element: me.value})
                    }
                }else if( obj.state == 'deleteRelation') {

                    var targetId = obj.element.sourceElement.elementView.id
                    me.value.relationEventInfo.some(function (eventItem, evtIdx) {
                        if(eventItem.eventValue.elementView.id == targetId){
                            me.value.relationEventInfo[evtIdx] = null
                            return
                        }
                    })
                    me.value.relationEventInfo = me.value.relationEventInfo.filter(n=>n)

                    if(me.value.boundedContext.id != null){
                        me.$EventBus.$emit(me.value.boundedContext.id,  {state: "updatePolicy", element: me.value})
                    }
                }


                if(obj.state == 'updateEvent') {
                    me.value.relationEventInfo.forEach(function (eventItem, evtIdx) {
                        if(eventItem.eventValue.elementView.id == obj.element.elementView.id) {
                            me.value.relationEventInfo[evtIdx].eventValue = JSON.parse(JSON.stringify(obj.element));
                        }
                    })

                    if(me.value.boundedContext.id != null){
                        me.$EventBus.$emit(me.value.boundedContext.id,  {state: "updatePolicy", element: me.value})
                    }

                } else if( obj.state == 'deleteEvent') {

                    var targetId = obj.element.elementView.id
                    me.value.relationEventInfo.some(function (eventItem, evtIdx) {
                        if(eventItem.eventValue.elementView.id == targetId){
                            me.value.relationEventInfo[evtIdx] = null
                            return
                        }
                    })
                    me.value.relationEventInfo = me.value.relationEventInfo.filter(n=>n)
                } else if( obj.state == 'updateBounded'){
                    console.log("no selected")
                    me.value.selected =false
                    me.value.boundedContext.name = obj.element.name;
                    me.value.boundedContext.id = obj.element.elementView.id

                    var sendObj = {
                        state: 'updatePolicy',
                        element: me.value
                    }

                    me.$EventBus.$emit(obj.element.elementView.id, sendObj)
                }


            })
        },
        methods: {
            deletePolicy() {
                var me = this

                return new Promise(function (resolve) {

                    me.$EventBus.$off(`${me.value.elementView.id}`);
                    var obj = {
                        state: "deletePolicy",
                        element: JSON.parse(JSON.stringify(me.value))
                    }

                    //aggregate notify
                    if (me.value.boundedContext.id != null){
                        me.$EventBus.$emit(`${me.value.boundedContext.id}`, obj)
                    }

                    //event && relation notify
                    if(me.value.relationEventInfo.length>0){
                        me.value.relationEventInfo.forEach(function (EvItem) {
                            //event
                            me.$EventBus.$emit(`${EvItem.eventValue.elementView.id}`, obj)
                            //relation
                            me.$EventBus.$emit(`${EvItem.relationId}`, obj)
                        })
                    }


                    me.$emit('update:value', null);
                    me.$EventBus.$emit('storage')
                    resolve()
                })
            }
        }
    }
</script>


