<template>
    <v-container>
        <v-row>
            <v-col cols="6" md="10">
                <v-text-field
                        label="Serarch Project"
                        outlined
                        v-model="search"
                ></v-text-field>
            </v-col>
            <v-col cols="1" md="2"
                   pro
            >
                <v-btn block x-large
                       color="green lighten-2"
                       @click="newProjectCheck()"
                >New Project
                </v-btn>
            </v-col>
        </v-row>

        Local
        <v-divider></v-divider>
        <v-row>
            <v-col
                    v-for="(item, key) in filteredLocalList"
                    :key="key"
                    cols="6"
                    md="4"
            >
                <v-card
                        outlined
                        class="mx-auto"
                        max-width="400"
                        max-height="400"
                >
                    <v-img
                            :src="item.img"
                            max-width="400"
                            max-height="300"

                    ></v-img>
                    <v-list-item three-line>
                        <v-list-item-content>
                            <v-list-item-title class="headline mb-1">{{item.name}}</v-list-item-title>
                            <v-list-item-subtitle>Author : {{item.email}}</v-list-item-subtitle>
                            <v-list-item-subtitle>Modified Date : {{item.date}}</v-list-item-subtitle>
                        </v-list-item-content>
                    </v-list-item>
                    <v-card-actions>
                        <v-btn text :to="toUrl(item.name, item.userUid,'init',0)">Edit</v-btn>
                    </v-card-actions>
                </v-card>
            </v-col>
            <v-col
                    cols="6"
                    md="4"
            >
                <v-card
                        class="mx-auto"
                        max-width="344"
                        align="center"
                >
                </v-card>
            </v-col>
        </v-row>


        <div v-if="filteredMineList.length > 0">
            Mine
            <v-divider></v-divider>
        </div>
        <v-row>
            <v-col
                    v-for="(item, key) in filteredMineList"
                    :key="key"
                    cols="6"
                    md="4"
            >
                <v-card
                        outlined
                        class="mx-auto"
                        max-width="400"
                        max-height="400"
                >
                    <v-img
                            :src="item.img"
                            max-width="400"
                            max-height="300"

                    ></v-img>
                    <v-list-item three-line>
                        <v-list-item-content>
                            <v-list-item-title class="headline mb-1">{{ item.name }}
                                <div style="font-size:15px; float: right">Ver.{{item.version}}</div>
                            </v-list-item-title>
                            <v-list-item-subtitle>Author : {{ item.author }}
                            </v-list-item-subtitle>
                            <v-list-item-subtitle>Modified Date : {{ item.date }}
                            </v-list-item-subtitle>
                        </v-list-item-content>
                    </v-list-item>
                    <v-card-actions>
                        <!-- toUrl(projectName, author, verName, type) -->
                        <v-btn text @click.native="$EventBus.$emit('chatKey', item.userUid)"
                               :to="toUrl(
                               item.name,
                               item.userUid,
                               item.versionName,
                               item.type)">
                            Edit
                        </v-btn>

                        <v-btn
                                color="red"
                                text
                                @click="deleteConfirm(item)"
                        >
                            Delete
                        </v-btn>
                    </v-card-actions>
                </v-card>

            </v-col>
            <v-col
                    cols="6"
                    md="4"
            >
                <v-card
                        class="mx-auto"
                        max-width="344"
                        align="center"
                >
                </v-card>
            </v-col>
        </v-row>

        <div v-if="filteredSharedList.length > 0">
            Shared
            <v-divider></v-divider>
        </div>
        <v-row>
            <v-col
                    v-for="(item, key) in filteredSharedList"
                    :key="key"
                    cols="6"
                    md="4"
            >
                <v-card
                        outlined
                        class="mx-auto"
                        max-width="400"
                        max-height="400"
                >
                    <v-img
                            :src="item.img"
                            max-width="400"
                            max-height="300"

                    ></v-img>
                    <v-list-item three-line>
                        <v-list-item-content>
                            <v-list-item-title class="headline mb-1">{{ item.name }}
                                <div style="font-size:15px; float: right">Ver.{{item.version}}</div>
                            </v-list-item-title>
                            <v-list-item-subtitle>Author : {{ item.author }}
                            </v-list-item-subtitle>
                            <v-list-item-subtitle>Modified Date : {{ item.date }}
                            </v-list-item-subtitle>
                        </v-list-item-content>
                    </v-list-item>
                    <v-card-actions>
                        <!--                        toUrl(projectName, author, verName, type) -->
                        <v-btn text @click.native="$EventBus.$emit('chatKey', item.userUid)"
                               v-if="isYours(item.userUid)"
                               :to="toUrl(
                               item.name,
                               item.userUid,
                               item.versionName,
                               item.type)">
                            Edit
                        </v-btn>

                        <v-btn text @click.native="$EventBus.$emit('chatKey', item.userUid)"
                               v-if="!isYours(item.userUid)"
                               :to="toUrl(
                               item.name,
                               item.userUid,
                               item.versionName,
                               item.type)">
                            View
                        </v-btn>

                        <v-btn
                                v-if="isYours(item.userUid)"
                                color="red"
                                text
                                @click="deleteConfirm(item)"
                        >
                            Delete
                        </v-btn>
                    </v-card-actions>
                </v-card>

            </v-col>
            <v-col
                    cols="6"
                    md="4"
            >
                <v-card
                        class="mx-auto"
                        max-width="344"
                        align="center"
                >
                </v-card>
            </v-col>
        </v-row>


        <v-dialog v-model="deleteDialog" persistent max-width="290">
            <v-card>
                <v-card-title class="headline">?</v-card-title>
                <v-card-text>
                    <div style="color: #8b0000"> </div>
                    ,  .
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red darken-1" text @click.native="deleteItem">Delete</v-btn>
                    <v-btn color="green darken-1" text @click.native="deleteDialog = false">Cancel</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="newProjectDialog" persistent max-width="350">
            <v-card>
                <v-card-title class="headline"> ?</v-card-title>
                <v-card-text>
                    <div style="color: #8b0000">,  .</div>
                      NOW Project .
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red darken-1" text @click.native="createNewProject()">NEW Project</v-btn>
                    <v-btn color="blue darken-1" text :to="toUrl(loginProjectName, loginUserUid, 'init', 0)" > NOW Project</v-btn>
                    <v-btn color="green darken-1" text @click.native="newProjectDialog = false" > close</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

    </v-container>
</template>

<script>
    import firebase from 'firebase';

    export default {
        name: "EventStormingListPage",
        data() {
            return {
                stormingLocalList: [],
                stormingSharedList: [],
                stormingMineList: [],
                //user info
                loginProjectName: '',
                loginUserUid: '',
                //search
                search: '',
                //delete info
                deleteDialog: false,
                selectDelete: [],
                //newProject
                newProjectDialog:false,
            }
        },
        created() {
            var me = this;

            if (window.localStorage.getItem("accessToken") != null) {
                me.getList()
                me.filteredLocalList
                me.filteredMineList
                me.filteredSharedList
            }else{
                me.getList()
                me.filteredLocalList
            }
            //new Project
            me.loginUserUid = localStorage.getItem("uid") != null ? localStorage.getItem("uid") : "anonymous"
            me.loginProjectName = localStorage.getItem("localSaveProjectName") != null ? localStorage.getItem("localSaveProjectName") : 'local'
        },
        computed: {
            filteredLocalList() {
                var me = this
                var filter = []
                for (var name in me.stormingLocalList) {
                    var ob = {
                        'email': me.stormingLocalList[name].author,
                        'date': me.stormingLocalList[name].date,
                        'img': me.stormingLocalList[name].img,
                        'name': me.stormingLocalList[name].name,
                        'userUid': me.stormingLocalList[name].userUid,
                        'version': me.stormingLocalList[name].version
                    }

                    filter.push(ob)
                }

                return filter
            },
            filteredMineList() {
                var me = this
                var filter = []
                for (var name in me.stormingMineList) {
                    var ob = {
                        'name': name,
                        'userUid': localStorage.getItem('uid'),
                        'img': me.stormingMineList[name][Object.keys(me.stormingMineList[name])[Object.keys(me.stormingMineList[name]).length - 1]].img,
                        'author': me.stormingMineList[name][Object.keys(me.stormingMineList[name])[Object.keys(me.stormingMineList[name]).length - 1]].author,
                        'date': me.stormingMineList[name][Object.keys(me.stormingMineList[name])[Object.keys(me.stormingMineList[name]).length - 1]].date,
                        'version': Object.keys(me.stormingMineList[name]).length,
                        'versionName': Object.keys(me.stormingMineList[name])[Object.keys(me.stormingMineList[name]).length - 1],
                        'type': false
                    }
                    filter.push(ob)
                }

                if (me.search) {
                    return filter.filter(eventstorming => {
                        return eventstorming.name.toLowerCase().includes(me.search.toLowerCase())
                    })
                } else {
                    return filter
                }

            },
            filteredSharedList() {
                var me = this
                var filter = [];
                for (var uid in me.stormingSharedList) {
                    for (var name in me.stormingSharedList[uid]) {
                        var ob = {
                            'name': name,
                            'userUid': uid,
                            'img': me.stormingSharedList[uid][name][Object.keys(me.stormingSharedList[uid][name])[Object.keys(me.stormingSharedList[uid][name]).length - 1]].img,
                            'author': me.stormingSharedList[uid][name][Object.keys(me.stormingSharedList[uid][name])[Object.keys(me.stormingSharedList[uid][name]).length - 1]].author,
                            'date': me.stormingSharedList[uid][name][Object.keys(me.stormingSharedList[uid][name])[Object.keys(me.stormingSharedList[uid][name]).length - 1]].date,
                            'version': Object.keys(me.stormingSharedList[uid][name]).length,
                            'versionName': Object.keys(me.stormingSharedList[uid][name])[Object.keys(me.stormingSharedList[uid][name]).length - 1],
                            'type': true
                        }
                        filter.push(ob)
                    }
                }

                if (me.search) {
                    return filter.filter(eventstorming => {
                        return eventstorming.name.toLowerCase().includes(me.search.toLowerCase())
                    })
                } else {
                    return filter
                }

            }

        },
        watch: { },
        mounted: function () {
            var me = this

            //login toaken info
            me.$EventBus.$on('login', function (newVal) {
                me.getList()
            })

            me.$EventBus.$on('updatePic', function (newVal) {
                me.getList()
                me.filteredLocalList
            })
        },
        beforeDestroy(){
            this.$EventBus.$off('updatePic')
        },
        methods: {
            createNewProject(){
                var me = this
                me.newProjectDialog = false
                localStorage.removeItem('localLoadData')
                localStorage.removeItem('localSaveImg')
                localStorage.removeItem('localSaveProjectName')
                me.$router.push({path: `${me.toUrl(me.loginProjectName, me.loginUserUid,'init',0)}` });
            },
            newProjectCheck(){
                var me = this

                if( JSON.parse(localStorage.getItem('localLoadData'))['definition'].length != 0){
                    me.newProjectDialog = true;
                }else{
                    // 
                    me.$router.push({path: `${me.toUrl(me.loginProjectName, me.loginUserUid,'init',0)}` });
                }
            },
            isYours(getUid) {

                if (localStorage.getItem("uid") == getUid) {
                    return true
                } else {
                    return false
                }

            },
            toUrl(projectName, author, verName, type) {
                return "storming/" + author + "/" + projectName + "/" + verName + "/" + type
            },
            deleteConfirm(item) {
                var me = this
                me.selectDelete = JSON.parse(JSON.stringify(item))
                me.deleteDialog = true
            },
            deleteItem() {
                var me = this;
                var deleteItem;

                if (me.selectDelete.type) {
                    deleteItem = firebase.database().ref('/saver/shared/' + me.selectDelete.userUid + "/" + me.selectDelete.name)
                } else {
                    deleteItem = firebase.database().ref('/saver/mine/' + me.selectDelete.userUid + "/" + me.selectDelete.name)
                }

                deleteItem.remove()
                me.$nextTick(function () {
                    me.getList()
                    me.deleteDialog = false
                })
            },
            getUid() {
                return new Promise(function (resolve) {
                    firebase.auth().onAuthStateChanged(function (user) {
                        if (user) {
                            resolve(user.uid);
                        } else {
                            resolve("everyone")
                        }
                    });
                })
            },
            getList() {
                var me = this

                // var userId = await me.getUid()
                var userId = localStorage.getItem("uid")

                var local = [
                    {
                        version: '0',
                        img: localStorage.getItem('localSaveImg') != null ? localStorage.getItem('localSaveImg') : 'https://github.com/sooheon45/eventstorming/blob/master/docs/media/evtstrm.jpg?raw=true',
                        name: localStorage.getItem("localSaveProjectName") != null ? localStorage.getItem("localSaveProjectName") : 'local',
                        userUid: localStorage.getItem("uid") != null ? localStorage.getItem("uid") : "anonymous",
                        author: localStorage.getItem("email") != null ? localStorage.getItem("email") : "anonymous@sample.org",
                        date: localStorage.getItem("localSaveTime") != null ? localStorage.getItem("localSaveTime") : "undefined"

                    }
                ]

                me.stormingLocalList = local

                if (userId) {
                    //firebase
                    firebase.database().ref('/saver/mine/' + userId).once('value').then(function (snapshot) {
                        me.stormingMineList = snapshot.val();
                    });
                    firebase.database().ref('/saver/shared/').once('value').then(function (snapshot) {
                        me.stormingSharedList = snapshot.val();
                    });
                }
            }
        }
    }
</script>

