<template>
    <!-- width 390 -->
    <v-layout wrap>
        <v-navigation-drawer v-model="value.drawer" absolute right v-bind:style="{width: widthScale}">

            <!--       -->
            <v-list class="pa-1">
                <v-list-item>
                    <v-list-item-avatar>
                        <img :src="img">
                    </v-list-item-avatar>
                    <v-list-item-title class="headline">{{ titleName }}
                    </v-list-item-title>
                    <v-tooltip v-model="show" top>
                        <template v-slot:activator="{ on }">
                            <v-btn icon @click="value.drawer =false">
                                <v-icon color="grey lighten-1">mdi-close</v-icon>
