                    </v-tooltip>
                </v-list-item>
            </v-list>

            <!--      -->
            <v-list class="pt-0" dense flat>
                <v-divider></v-divider>
                <v-card v-if="value._type == 'org.uengine.modeling.model.Relation' && value.sourceElement._type == 'org.uengine.modeling.model.Event'">
                    <v-card-text>
                        <span class="headline">Type</span>
                        <v-layout>
                            <v-col>
                                <v-radio-group v-model="value.relationKey" style="padding-left: 10px;">
                                    <v-radio label="Event-Driven(Pub/Sub)" value="RestAPI"></v-radio>
                                    <v-radio label="Request & Response(Restful)" value="Restful"></v-radio>
                                </v-radio-group>
                                <v-select :disabled="value.relationKey == 'RestAPI'"
                                          v-model="value.relationValue"
                                          :items="restfulList"
                                          label="Request & Response(Restful)"></v-select>
                            </v-col>
                        </v-layout>
                    </v-card-text>
                </v-card>
                <v-card outlined v-else>
                    <v-card-text>
                        <span class="headline" v-if="titleName">{{titleName}} Name</span>
                        <v-textarea name="input-7-1" outline :label="'Input Text'" auto-grow
                                    v-model="value.name"></v-textarea>

                        <v-card outlined v-if="usedTranslate">
                            <v-card-text @click="changeTranslate()">
                                  : {{ translateText }}
                            </v-card-text>
                            <v-card-text>
                                  .
                            </v-card-text>
                        </v-card>


                        <span class="headline" v-if="value._type == 'org.uengine.modeling.model.Event'">Trigger</span>
                        <v-layout v-if="value._type == 'org.uengine.modeling.model.Event'" flat>
                            <v-radio-group v-model="value.trigger" style="font-size: small">
                                <v-row>
                                    <v-col>
                                        <v-radio label="Pre Persist" value="@PrePersist"></v-radio>
                                        <v-radio label="Pre Update" value="@PreUpdate"></v-radio>
                                        <v-radio label="Pre Delete" value="@PreDelete"></v-radio>
                                    </v-col>
                                    <v-col>
                                        <v-radio label="Post Persist" value="@PostPersist"></v-radio>
                                        <v-radio label="Post Update" value="@PostUpdate"></v-radio>
                                        <v-radio label="Post Delete" value="@PostDelete"></v-radio>
                                    </v-col>
                                </v-row>
                            </v-radio-group>
                        </v-layout>

                        <span class="headline"
                              v-if="value._type == 'org.uengine.modeling.model.Event' ||
                              value._type == 'org.uengine.modeling.model.Aggregate'">Attributes</span>
                        <v-layout
                                v-if="value._type == 'org.uengine.modeling.model.Event'"
                                flat>
                            <v-col>
                                <v-data-table
                                        :headers="headers"
                                        :items="fieldDescriptors"
                                        hide-default-header
                                        hide-default-footer
                                        class="elevation-1"
                                >

                                    <template v-slot:item.action="{ item }">
                                        <v-icon
                                                small
                                                class="mr-2"
                                                @click="editEntity(item)"
                                        >
                                            edit
                                        </v-icon>
                                        <v-icon
                                                small
                                                @click="deleteEntity(item)"
                                        >
                                            delete
                                        </v-icon>
                                    </template>
                                </v-data-table>


                                <v-row justify="center">
                                    <!--                                        <v-checkbox v-model="entityKey" type="text"></v-checkbox>-->
                                    <v-select style="width: 30px" v-model="entityType" :items="entityTypeList"
                                              label="Type"></v-select>
                                    <v-text-field v-model="entityName" label="Name"
                                                  v-on:keyup.enter="attributeADD(entityType,entityName)"
                                                  required></v-text-field>
                                </v-row>
                                <v-row justify="end">
                                    <v-btn v-if="value._type == 'org.uengine.modeling.model.Event'" text
                                           @click="syncFromAggregate()">
                                        <v-icon color="grey lighten-1" small>mdi-refresh</v-icon>
                                        Sync attributes
                                    </v-btn>
                                    <v-btn rounded color="primary" @click="attributeADD(entityType,entityName)" dark>
                                        ADD ATTRIBUTE
                                    </v-btn>
                                </v-row>
                            </v-col>
                        </v-layout>

                        <v-layout
                                v-else-if="value._type == 'org.uengine.modeling.model.Aggregate'"
                                flat>
                            <v-col>
                                <v-data-table
                                        :headers="headers"
                                        :items="value.aggregateRoot.fieldDescriptors"
                                        hide-default-header
                                        hide-default-footer
                                        class="elevation-1"
                                >

                                    <template v-slot:item.isKey="{ item }">
                                        <v-icon
                                                class="mr-2"
                                                small
                                                v-if="item.isKey"
                                        >
                                            mdi-key
                                        </v-icon>
                                    </template>

                                    <template v-slot:item.action="{ item }">
                                        <v-icon
                                                small
                                                class="mr-2"
                                                @click="editEntity(item)"
                                        >
                                            edit
                                        </v-icon>
                                        <v-icon
                                                class="mr-2"
                                                v-if="!item.isKey"
                                                small
                                                @click="deleteEntity(item)"
                                        >
                                            delete
                                        </v-icon>
                                    </template>

                                </v-data-table>


                                <v-row justify="center">
                                    <v-flex xs4>
                                        <v-select v-model="entityType" :items="entityTypeList" label="Type"
                                                  style="margin-right: 15px;"></v-select>
                                    </v-flex>
                                    <v-flex xs6>
                                        <v-text-field v-model="entityName" label="Name"
                                                      v-on:keyup.enter="attributeADD(entityType,entityName)"
                                                      required></v-text-field>
                                    </v-flex>
                                </v-row>
                                <v-row justify="end">
                                    <v-btn rounded color="primary" @click="attributeADD(entityType,entityName)" dark>
                                        ADD ATTRIBUTE
                                    </v-btn>
                                </v-row>
                            </v-col>
                        </v-layout>

                        <v-layout v-if="value._type == 'org.uengine.modeling.model.Command'">
                            <v-autocomplete v-model="value.restfulType" :items="restfulList"
                                            label="Restful TYPE" persistent-hint>
                            </v-autocomplete>
                        </v-layout>


                        <span class="headline"
                              v-if="value._type == 'org.uengine.modeling.model.Event' || value._type == 'org.uengine.modeling.model.Command' || value._type == 'org.uengine.model.External'">Associated Aggregate</span>
                        <v-layout
                                v-if="value._type == 'org.uengine.modeling.model.Event' || value._type == 'org.uengine.modeling.model.Command' || value._type == 'org.uengine.model.External'"
                                flat>
                            <v-col>
                                <v-autocomplete
                                        v-model="value.aggregate"
                                        style="margin-left: 20px; margin-right: 20px;"
                                        :items="aggregateList"
                                        item-text="name"
                                        return-object
                                        label="Select Aggregate" persistent-hint>
                                </v-autocomplete>
                            </v-col>
                        </v-layout>
                        <v-layout
                                v-if="value._type == 'org.uengine.modeling.model.View'"
                                style="width: auto;"

                        >
                            <v-card-text>
                                <span class="headline">Attributes</span>
                                <v-data-table
                                        :headers="headers"
                                        :items="value.fieldDescriptors"
                                        hide-default-header
                                        hide-default-footer
                                        class="elevation-1"
                                >

                                    <template v-slot:item.action="{ item }">
                                        <v-icon
                                                small
                                                class="mr-2"
                                                @click="editEntity(item)"
                                        >
                                            edit
                                        </v-icon>
                                        <v-icon
                                                small
                                                @click="deleteEntity(item)"
                                        >
                                            delete
                                        </v-icon>
                                    </template>
                                </v-data-table>


                                <v-row justify="center">
                                    <v-select style="width: 30px" v-model="entityType" :items="entityTypeList"
                                              label="Type"></v-select>
                                    <v-text-field v-model="entityName" label="Name"
                                                  v-on:keyup.enter="attributeADD(entityType,entityName)"
                                                  required></v-text-field>
                                </v-row>
                                <v-row justify="end">
                                    <v-btn rounded color="primary" @click="attributeADD(entityType,entityName)"
                                           dark>
                                        ADD ATTRIBUTE
                                    </v-btn>
                                </v-row>


                                <v-radio-group
                                        v-model="value.dataProjection"
                                        style="padding-left: 10px; " row>
                                    <v-radio label="CQRS" value="cqrs"></v-radio>
                                    <v-radio disabled label="UI Mashup" value="uimashup"></v-radio>
                                    <v-radio disabled label="GraphQL" value="graphql"></v-radio>
                                </v-radio-group>


                                <!-- create -->
                                <v-col
                                        v-for="(item, key) in value.viewFieldDescriptors.createFieldDescriptors"
                                >
                                    <ViewCreate
                                            v-model="value"
                                            :createItem="item"
                                            :index="key"
                                            :events.sycn="eventList"
                                    ></ViewCreate>
                                </v-col>
                                <v-row align="center" justify="end">
                                    <v-btn text @click="viewRowAdd('create')">
                                        <v-icon class="outlined"> mdi-plus</v-icon>
                                    </v-btn>
                                </v-row>
                                <!-- create  end-->

                                <!-- update -->
                                <v-divider></v-divider>
                                <v-col
                                        v-for="(item, key) in value.viewFieldDescriptors.updateFieldDescriptors"
                                >
                                    <ViewUpdate
                                            v-model="value"
                                            :updateItem="item"
                                            :index="key"
                                            :events.sycn="eventList"
                                    ></ViewUpdate>
                                </v-col>
                                <v-row align="center" justify="end">
                                    <v-btn text @click="viewRowAdd('update')">
                                        <v-icon class="outlined"> mdi-plus</v-icon>
                                    </v-btn>
                                </v-row>
                                <!-- create end-->

                                <!-- delete -->
                                <v-divider></v-divider>

                                <v-col
                                        v-for="(item, key) in value.viewFieldDescriptors.deleteFieldDescriptors"
                                >
                                    <ViewDelete
                                            v-model="value"
                                            :deleteItem="item"
                                            :index="key"
                                            :events.sycn="eventList"
                                    ></ViewDelete>
                                </v-col>
                                <v-row align="center" justify="end">
                                    <v-btn text @click="viewRowAdd('delete')">
                                        <v-icon class="outlined"> mdi-plus</v-icon>
                                    </v-btn>
                                </v-row>
                                <!-- delete end -->
                            </v-card-text>
                        </v-layout>


                        <v-layout v-else-if="value._type == 'org.uengine.modeling.model.Aggregate'">
                            <v-divider dark style="margin-top: 10px; margin-bottom: 10px;"></v-divider>
                            <v-btn block color="info" rounded @click="umlDiagramOpen()"> Edit Domain Model by UML
                            </v-btn>
                        </v-layout>

                    </v-card-text>
                </v-card>
            </v-list>
        </v-navigation-drawer>
    </v-layout>

</template>


<script>
    var _ = require('lodash')
    var googleTranslate = require('google-translate')(process.env.VUE_APP_TRANSLATE_KEY);
    var changeCase = require('change-case');
    var pluralize = require('pluralize');
    //https://www.npmjs.com/package/change-case

    export default {
        name: 'modeling-property-panel',
        props: {
            value: Object,
            titleName: String,
            img: String,
        },
        computed: {
            fieldDescriptors: function () {
                var me = this
                return me.value.fieldDescriptors
            },
            widthScale: function () {
                var me = this;
                if (me.value._type == 'org.uengine.modeling.model.View') {
                    return 500
                } else {
                    return 390
                }
            },
            eventList: function () {
                var designer = this.$parent.getComponent('modeling-designer')

                var tmp = []

                designer.value.definition.forEach(function (tmpData) {
                    if (tmpData != null) {
                        if (tmpData._type == 'org.uengine.modeling.model.Event')
                            tmp.push(tmpData)
                    }

                })

                tmp = tmp.filter(n => n)
                return tmp
            },
            aggregateList: function () {
                var designer = this.$parent.getComponent('modeling-designer')

                var tmp = []

                designer.value.definition.forEach(function (tmpData) {
                    if (tmpData != null) {
                        if (tmpData._type == 'org.uengine.modeling.model.Aggregate') {
                            var ob = {
                                'id': tmpData.elementView.id,
                                'name': tmpData.name,
                                'namePlural': tmpData.namePlural,
                                'namePascalCase': tmpData.namePascalCase,
                                'nameCamelCase': tmpData.nameCamelCase,
                                'boundedContext': tmpData.boundedContext,
                                'fieldDescriptors': tmpData.aggregateRoot['fieldDescriptors']
                            }
                        }
                        tmp.push(ob)
                    }
                })

                tmp = tmp.filter(n => n)
                return tmp
            },
        },
        data: function () {
            return {
                show: false,
                navigationDrawer: false,
                _item: this.value,
                preventWatch: false,
                x: null,
                y: null,
                width: null,
                height: null,
                style: [],
                angle: null,
                connectedList: [],
                connectedListName: {},
                viewList: null,
                viewRowAddDialog: false,

                restfulList: ['GET', 'POST', 'PATCH', 'DELETE'],
                entityTypeList: ['Integer', 'String', 'Float', 'Double', 'Long', 'Date'],
                entityKey: false,
                entityType: '',
                entityName: '',
                headers: [
                    {
                        text: 'type',
                        value: 'className',
                        align: 'center'
                    },
                    {
                        text: 'name',
                        value: 'name',
                        align: 'center'
                    },
                    {
                        text: 'Actions',
                        value: 'action',
                        sortable: false,
                        align: 'center'
                    },],
                translateText: '',
                usedTranslate: false,
                UMLInput: '',
                editedIndex: '',
                selected: [],

                //view
                createSelectEvent: [],
                entityActionList: ['create', 'update', 'delete'],
                entityAction: '',
                viewCreateTableViewCol: {},
                viewCreateTableEventCol: {},

            }
        },
        created: function () {

        },
        mounted: function () {
        },
        watch: {
            "value.relationValue": function (newVal, oldVal) {
                var me = this
                var st = "create"
                if (oldVal)
                    st = "modify"


                var poObj = {
                    state: st,
                    element: me.value

                }

                me.$EventBus.$emit(`${me.value.sourceElement.elementView.id}`, poObj)
                me.$EventBus.$emit(`${me.value.targetElement.elementView.id}`, poObj)


            },
            "value.relationKey": function (newVal) {
                var me = this

                // me.value.relationValue = newVal
                if (newVal == 'RestAPI') {
                    me.value.relationValue = 'Pub/Sub'
                } else {
                    //restful
                }


            },
            "value.name": function (newVal, oldVal) {
                var me = this
                if (oldVal == 'tmp') {
                    me.getTranslate(newVal)
                    me.initNameCase(newVal)
                    me.translateText = '';
                } else {
                    me.getTranslate(newVal)
                    me.setNameCase(newVal)
                    me.translateText = '';
                }
            },
            "value.drawer": function (val) {
                this.navigationDrawer = val;
            },
            //      .
            navigationDrawer: {
                handler: function (val, oldval) {
                    var opengraph = this.$parent.getComponent('opengraph')
                    if (this.titleName == 'Aggregate') {
                        // this.getRelation()
                    }
                    if (val == true) {
                        this._item = this.value;

                        if (this.value.elementView) {
                            this.x = this.value.elementView.x;
                            this.y = this.value.elementView.y;
                            this.width = this.value.elementView.width;
                            this.height = this.value.elementView.height;
                        }

                        this.$emit('update:drawer', val);

                    } else {
                        // 
                        this.value.name = this.value.name.replace(/\n/g, "").replace(/ /gi, "")

                        //  .
                        this.$emit('update:drawer', false);
                    }
                }
            },

        },
        methods: {
            deleteCreateEntity(key, index) {
                console.log(key, index)

                var me = this
                me.value.viewFieldDescriptors.some(function (views, idx) {
                    if (idx == key) {
                        if (idx > -1) {
                            me.value.viewFieldDescriptors[idx].fieldDescriptors.splice(index, 1)
                        }
                    }
                })
            },
            createTablePush(key) {

                var me = this
                // viewCreateTableViewCol
                // viewCreateTableEventCol
                var obj = {
                    viewCol: me.viewCreateTableViewCol,
                    eveCol: me.viewCreateTableEventCol
                }
                me.value.viewFieldDescriptors.some(function (ele, idx) {
                    if (idx == key) {
                        me.value.viewFieldDescriptors[key].fieldDescriptors.push(obj)
                    }
                })


            },
            arrayDiff(a1, a2) {

                var a = [], diff = [];

                for (var i = 0; i < a1.length; i++) {
                    a[a1[i]] = true;
                }

                for (var i = 0; i < a2.length; i++) {
                    if (a[a2[i]]) {
                        delete a[a2[i]];
                    } else {
                        a[a2[i]] = true;
                    }
                }

                for (var k in a) {
                    diff.push(k);
                }

                return diff;
            },
            syncFromAggregate() {
                var me = this
                var arr2 = me.value.aggregate.fieldDescriptors
                var arr1 = me.value.fieldDescriptors

                arr2.forEach(function (agg) {
                    var check = false
                    arr1.forEach(function (ev) {
                        if (JSON.stringify(Object.entries(agg).sort()) === JSON.stringify(Object.entries(ev).sort())) {
                            check = true
                        }
                    })
                    if (!check) {
                        me.value.fieldDescriptors.push(agg)
                    }
                })

            },
            viewEntityEdit() {
                var me = this
                me.value.editingView = true
            },
            viewdeleteItem(item) {
                var me = this
                const index = me.value.viewFieldDescriptors.indexOf(item)
                // confirm('Are you sure you want to delete this item?')
                confirm('Are you sure you want to delete this item?') && me.value.viewFieldDescriptors.splice(index, 1)
            },
            viewRowAdd(type) {
                var me = this

                if (me.value.dataProjection == 'cqrs') {


                    if (type == 'create') {

                        var obj = {
                            sourceEvent: [],
                            fieldMapping: [{"viewField":{},"eventField":{}}],
                        }
                        me.value.viewFieldDescriptors.createFieldDescriptors.push(obj);
                    } else if (type == 'update') {
                        var obj = {
                            sourceEvent: [],
                            fieldMapping: [{"viewField":{},"eventField":{}}],
                            condition: [{"conditionViewField":{},"conditionEventField":{}}]
                        }
                        me.value.viewFieldDescriptors.updateFieldDescriptors.push(obj);
                    } else if (type == 'delete') {

                        var obj = {
                            sourceEvent: [],
                            condition: [{"conditionViewField":{},"conditionEventField":{}}]
                        }
                        me.value.viewFieldDescriptors.deleteFieldDescriptors.push(obj);
                    }


                } else if (me.value.dataProjection == 'uimashup') {
                    // var ob = {
                    //     isKey: false,
                    //     name: '',
                    //     className: '',
                    //     sourceRepository: '',
                    //     repositoryDirectValue: '',
                    //     hateoas: '',
                    //     link: ''
                    // }
                    // me.value.viewFieldDescriptors.push(ob);
                }
            },
            save() {
                var me = this

                if (this.editedIndex > -1) {
                    Object.assign(me.value.fieldDescriptors[this.editedIndex], this.editedItem)
                } else {
                    me.value.fieldDescriptors.push(this.editedItem)
                }
                me.value.editingView = false
            },
            editItem(item) {
                var me = this
                me.editedIndex = me.value.fieldDescriptors.indexOf(item)

                // this.editedItem = Object.assign({}, item)
                // me.value.editingView  = true
            },
            setNameCase: _.debounce(
                function (newVal) {
                    var me = this
                    if (me.value._type == 'org.uengine.modeling.model.View' || me.value._type == 'org.uengine.modeling.model.Event' || me.value._type == 'org.uengine.modeling.model.Command' || me.value._type == 'org.uengine.modeling.model.Policy' || me.value._type == 'org.uengine.modeling.model.Aggregate') {
                        me.value.namePascalCase = changeCase.pascalCase(newVal)
                        me.value.nameCamelCase = changeCase.camelCase(newVal)
                        me.value.namePlural = changeCase.camelCase(pluralize(newVal));
                    }
                }, 200
            ),
            initNameCase:
                function (newVal) {
                    var me = this

                    if (me.value._type == 'org.uengine.modeling.model.View' || me.value._type == 'org.uengine.modeling.model.Event' || me.value._type == 'org.uengine.modeling.model.Command' || me.value._type == 'org.uengine.modeling.model.Policy' || me.value._type == 'org.uengine.modeling.model.Aggregate') {
                        me.value.namePascalCase = changeCase.pascalCase(newVal)
                        me.value.nameCamelCase = changeCase.camelCase(newVal)
                        me.value.namePlural = changeCase.camelCase(pluralize(newVal));
                    }
                },
            getTranslate:
                _.debounce(
                    function (newVal) {
                        var me = this
                        if (me.value._type == 'org.uengine.modeling.model.Event') {
                            try {
                                googleTranslate.detectLanguage(newVal, function (err, detection) {
                                    if (detection.language != 'en') {
                                        googleTranslate.translate(newVal, 'en', function (err, translation) {
                                            me.usedTranslate = true
                                            me.translateText = changeCase.pascalCase(translation.translatedText);
                                        });
                                    }
                                });
                            } catch (e) {
                                // console.log('[Event ERROR]' + e)
                            }

                        } else if (me.value._type == 'org.uengine.modeling.model.Policy') {

                            try {
                                googleTranslate.detectLanguage(newVal, function (err, detection) {
                                    if (detection.language != 'en') {
                                        googleTranslate.translate(newVal, 'en', function (err, translation) {
                                            me.usedTranslate = false
                                            me.translateText = changeCase.pascalCase(translation.translatedText);
                                        });
                                    } else if (detection.language == 'en') {
                                        me.usedTranslate = false
                                        me.translateText = changeCase.pascalCase(newVal)
                                    }
                                });
                            } catch (e) {
                                // console.log('[Policy ERROR]' + e)
                            }

                        } else if (me.value._type == 'org.uengine.modeling.model.Aggregate') {

                            try {
                                googleTranslate.detectLanguage(newVal, function (err, detection) {
                                    if (detection.language != 'en') {
                                        googleTranslate.translate(newVal, 'en', function (err, translation) {
                                            me.usedTranslate = true
                                            me.translateText = changeCase.pascalCase(translation.translatedText);
                                        });
                                    } else if (detection.language == 'en') {
                                        me.usedTranslate = true
                                        me.translateText = changeCase.pascalCase(newVal)
                                    }
                                });
                            } catch (e) {
                                // console.log('[Aggregate ERROR]' + e)
                            }

                        } else {
                            try {
                                googleTranslate.detectLanguage(newVal, function (err, detection) {
                                    if (detection.language != 'en') {
                                        googleTranslate.translate(newVal, 'en', function (err, translation) {
                                            me.usedTranslate = true
                                            me.translateText = translation.translatedText;
                                        });
                                    } else if (detection.language == 'en') {
                                        me.usedTranslate = true
                                    }
                                });
                            } catch (e) {
                                // console.log(e)
                            }

                        }
                    }, 500
                ),
            umlDiagramOpen() {
                var me = this
                // console.log("umlDiagram-Open")
                me.$ModelingBus.$emit('umlDiagram');
            },
