<script>
    import TextReader from "@/components/yaml.vue";
    import CodeViewer from "../CodeViewer";
    import {
        saveAs
    } from 'file-saver';
    import firebase from 'firebase'
    import {
        mdiAbTesting
    } from '@mdi/js';

    var _ = require('lodash')

    var Base64 = require('js-base64').Base64;
    var yamlpaser = require('js-yaml');
    var FileSaver = require('file-saver');
    var changeCase = require('change-case');
    var pluralize = require('pluralize');
    var JSZip = require('jszip')
    var path = require('path')
    import {diffString, diff} from "json-diff";


    export default {
        name: 'modeling-designer',
        components: {
            TextReader,
            saveAs,
            CodeViewer,
            mdiAbTesting,
            diffString, diff
        },
        props: {
            elementTypes: Array,
        },
        data() {
            return {
                loadingDialog: true,
                isShare: false,
                codeViewing: false,
                saveItems: [
                    {title: 'Save to Server'},
                    {title: 'Download model File'}
                ],
                openItems: [
                    {title: 'Open from Files'},
                    // {title: 'open from Local'},
                ],
                codeItems: [
                    {title: 'Code Preview'},
                    {title: 'Download Archive'},
                ],
                onlineSaveDialog: false,
                onlineSaveComfirmDialog: false,
                isLoadVersion: false,
                //server
                author: '', //email
                serverComment: '',
                serverDate: '',
                editing: true,
                files: {
                    version: 'mdi-server',
                    code: 'mdi-code-array',
                    save: 'mdi-content-save',
                    open: 'mdi-book-open',
                    md: 'mdi-markdown',
                    txt: 'mdi-file-document-outline',
                    java: 'mdi-language-java',
                    xml: 'mdi-xml',
                    shell: 'mdi-powershell',
                    docker: 'mdi-docker',
                    png: 'mdi-file-image',
                    json: 'mdi-json',
                    python: 'mdi-language-python'
                },
                canvas: null,
                dragPageMovable: false,
                value: {
                    'definition': [],
                    'relation': []
                },
                projectName: '',
                generateZipDialog: false,
                //undo Redo
                undoRedoStorage: 5,
                undoRedoArray: [],
                undoRedoIndex: 0,
                undoRedoLastIndex: 0,
                imageBase: 'https://raw.githubusercontent.com/kimsanghoon1/k8s-UI/master/public/static/image/symbol/',
                // 
                snackbar: false,
                color: 'error',
                mode: 'multi-line',
                timeout: 6000,
                text: '.',
                //Zip
                pathTmp: [],
                gitCodeUrl: {},
                //data structure
                model: [],
                fullPathList: [],
                treeList: [],
                getCodeList: [],
                openCode: [],
                template: '',
                treeOpen: true,
                gitUrl: '',
                gitPath: [],
                gitCode: [],
                checkName: true,
                // Children
                drawer: false,
                chatUid: '',
                messageRef: {},
                //version
                versionList: [],
                version: '',
                revisionInfo: {},
                showVersionsDialog: false,
                dialog2: false,
                loginAuthor: '',
                loginProjectName: '',
                loginVersion: '',
                loginType: '',
                //view
                viewSelect:{},
                viewOpenId: '',
                viewRowAddDialog: false,
                viewSelectType: 'cqrs',
                entityActionList: ['create', 'update', 'delete'],
                entityAction: '',
                //view create
                viewColumnTypeList: ['String', 'Integer', 'Double'],
                selectEvent: [],
                viewColumnType: '',
                viewColumn: '',
                sourceEventColumn: '',
                defaultValue: '',

            }
        },
        beforeDestroy: function () {
            var me = this

            me.$refs['modeler-image-generator'].save(me.id, me.canvas).then(function (resolve) {

                if (me.loginType == '0') {
                    localStorage.setItem('localSaveImg', resolve)
                } else if (me.loginType == 'true') {
                    //share
                    firebase.database().ref('saver/shared/' + me.loginAuthor + "/" + me.loginProjectName + "/" + me.loginVersion)
                        .update({
                            img: resolve
                        });
                } else if (me.loginType == 'false') {
                    //mine
                    firebase.database().ref('saver/mine/' + me.loginAuthor + "/" + me.loginProjectName + "/" + me.loginVersion)
                        .update({
                            img: resolve
                        });
                }
                me.$EventBus.$emit('updatePic', resolve)

            })

            // me.canvas.removeSlider();
            // me.$EventBus.$emit('closeChat')
            // me.messageRef.off();


        },
        computed: {
            eventList: function () {
                var me = this
                var tmp = []
                me.value.definition.forEach(function (tmpData) {
                    if (tmpData != null) {
                        if (tmpData._type == 'org.uengine.modeling.model.Event')
                            tmp.push(tmpData)
                    }

                })

                tmp = tmp.filter(n => n)
                return tmp
            },
            templateList: function () {
                var me = this
                return Object.keys(me.$manifestsPerTemplate)
            },
            id: {
                get: function () {
                    return this.projectName
                }
            },
        },
        created: function () {
            var me = this
            // var getAuthor = this.$route.params.author;
            // var getProject = this.$route.params.projectName;
            // var getVersion = this.$route.params.version;
            // var getType = this.$route.params.type;
            me.loginAuthor = this.$route.params.author;
            me.loginProjectName = this.$route.params.projectName;
            me.loginVersion = this.$route.params.version;
            me.loginType = this.$route.params.type;

            var valueInit = {"definition": [], "relation": []}

            var loginuserId = localStorage.getItem('uid')
            // 
            me.projectName = me.loginProjectName

            me.$nextTick(function () {
                if (me.loginType == '0') {
                    if (localStorage.getItem('localLoadData') != null) {
                        me.value = JSON.parse(localStorage.getItem('localLoadData'));
                        me.$nextTick(function () {
                            me.loadingDialog = false;
                        })

                    } else {
                        me.value = valueInit
                        me.$nextTick(function () {
                            me.loadingDialog = false;
                        })

                    }
                } else if (me.loginType == 'true') {
                    //share
                    firebase.database().ref("/saver/shared/" + me.loginAuthor + "/" + me.loginProjectName + "/" + me.loginVersion + "/").once('value').then(function (snapshot) {
                        me.value = JSON.parse(snapshot.val().value)
                        me.$nextTick(function () {
                            me.loadingDialog = false;
                        })

                    });
                } else if (me.loginType == 'false') {
                    //mine
                    firebase.database().ref("/saver/mine/" + me.loginAuthor + "/" + me.loginProjectName + "/" + me.loginVersion).once('value').then(function (snapshot) {
