                    // console.log(val)
                    me.chatUid = val;
                    me.messageRef = firebase.database().ref(`/${me.$route.params.author}/${me.$route.params.projectName}`);
                })
            }

            me.$EventBus.$on('viewRowAddDialog', function (newVal) {
                me.viewRowAddDialog = newVal.state
