            fullPathList: function (newVal) {
                var me = this
                me.treeList = newVal.reduce((children, value) => {
                    value.fullPath.split('/').reduce((folder, o, i) => {

                        if (i === 0) {
                            var p = folder;
                        } else {
                            var p = folder.children = folder.children || [];
                        }

                        let f = p.find(x => x.name === o.trim());

                        if (!f) {

                            f = {
                                name: o.trim(),
                                key: me.findFile(value.key, o.trim()).key,
                                file: me.findFile(value.key, o.trim()).file,
                                code: me.findFile(value.key, o.trim()).code,
                            }

                            p.push(f);
                        }

                        return f;
                    }, children);

                    return children;
                }, []);
