            gitTemplate() {
                var me = this
                return new Promise(function (resolve, reject) {
                    me.gitUrl = window.prompt("Please Input Github Repository URL");
                    me.$manifestsPerTemplate["GIT"] = [];
                    if (!window.localStorage.getItem("gitAccessToken")) {
                        var gitAccessToken = window.prompt("Please Input Your Github AccessToken (Only Authorization)")
                        me.$http.defaults.headers.common['Authorization'] = `token ${gitAccessToken}`;
                        localStorage.setItem('gitAccessToken', gitAccessToken);
                    }

                    me.gitRepository = me.gitUrl.split('/')[me.gitUrl.split('/').length - 2]
                    me.gitProject = me.gitUrl.split('/')[me.gitUrl.split('/').length - 1]

                    me.$http.get(`https://api.github.com/repos/` + me.gitRepository + '/' + me.gitProject + `/commits`).then(function (result) {
                        me.$http.get(`https://api.github.com/repos/` + me.gitRepository + '/' + me.gitProject + `/git/trees/` + result.data[0].sha).then(async function (test) {
                            await me.setGitList(test.data, me.gitRepository)
                            resolve()
                        })
                    })
                })
            },
            setGitList(element, repository) {
                var me = this
                me.fullPathList = []
                me.getCodeList = []
                me.gitPath = []
                me.gitCode = []
                return new Promise(function (resolve) {
                    try {
                        me.$http.get(element.url + '?recursive=1').then(function (result) {
                            result.data.tree.forEach(function (ele, idx) {

                                var fileName = ele.path.split('/')[ele.path.split('/').length - 1]
                                // var source = ''
                                if (ele.type != 'tree') {
                                    me.$manifestsPerTemplate["GIT"].push('./' + ele.path)
                                    me.gitCodeUrl[ele.path] = ele.url;
                                    // console.log(me.gitCodeUrl)
                                    // console.log(me.$manifestsPerTemplate)
                                }

                                if (result.data.tree.length - 1 == idx) {
                                    resolve()
                                }
                            })
                        }).catch(error => console.log(error))
                    } catch (e) {
                        // console.log(e)
                    }
                })


                // me.fullPathList = me.gitPath
                // me.getCodeList = me.gitCode

            },
            getListSetting() {
                var me = this
                return new Promise(async function (resolve, reject) {
                    let tmpList = []
                    me.fullPathList = []
                    me.getCodeList = []
                    me.treeList = []
                    me.model = {
                        _type: "org.uengine.modeling.model.Model",
                        boundedContexts: [],
                        projectName: me.projectName
                    }

                    // return new Promise(async function (resolve, reject) {

                    // })

//
                    //boundedContexts 
                    me.value.definition.forEach(function (item) {
                        if (item != null) {
                            if (item._type == 'org.uengine.modeling.model.BoundedContext') {
                                me.model.boundedContexts.push(item)
                            }
                        }
                    })

                    var modelForElements = {
                        BoundedContext: [],
                        AggregateRoot: [],
                        Aggregate: [],
                        Policy: [],
                        Event: [],
                        Command: [],
                        View: [],
                        RelationEventInfo: []
                    };

                    me.model.boundedContexts.forEach(function (bound, idx) {
                        bound.portGenerated = 8081 + idx
                        modelForElements["BoundedContext"].push(bound);

                        if (me.model.boundedContexts[idx].aggregates.length >= 1) {
                            me.model.boundedContexts[idx].aggregates.forEach(function (agg, idx) {
                                // fieldDescriptors 
                                agg.aggregateRoot.fieldDescriptors.forEach(function (fd) {
                                    fd.nameCamelCase = _.camelCase(fd.name)
                                    fd.namePascalCase = fd.nameCamelCase.substring(0, 1).toUpperCase() + fd.nameCamelCase.substring(1);
                                    if (fd.isKey)
                                        agg.aggregateRoot.keyFieldDescriptor = fd;
                                })
                                modelForElements["Aggregate"].push(agg);
                                modelForElements["AggregateRoot"].push(agg.aggregateRoot);
                                if (agg.commands)
                                    agg.commands.forEach(function (co) {
                                        if (co.name == '') {
                                            me.text = "Check Element Name"
                                            me.snackbar = true
                                            me.color = "error"
                                            me.timeout = 1000
                                            me.checkName = false
                                        }
                                        modelForElements["Command"].push(co);
                                    })

                                if (agg.events)
                                    agg.events.forEach(function (ev) {
                                        if (ev.name == '') {
                                            me.text = "Check Element Name"
                                            me.snackbar = true
                                            me.timeout = 1000
                                            me.color = "error"
                                            me.checkName = false
                                        }
                                        modelForElements["Event"].push(ev);
                                    })

                                if (agg.viewes)
                                    agg.viewes.forEach(function (vi) {
                                        if (vi.name == '') {
                                            me.text = "Check Element Name"
                                            me.snackbar = true
                                            me.timeout = 1000
                                            me.color = "error"
                                            me.checkName = false
                                        }
                                        modelForElements["View"].push(vi);
                                    })
                            })
                        }

                        if (me.model.boundedContexts[idx].policies.length >= 1) {
                            me.model.boundedContexts[idx].policies.forEach(function (po) {
                                if (po.name == '') {
                                    me.text = "Check Element Name"
                                    me.snackbar = true
                                    me.timeout = 1000
                                    me.color = "error"

                                    me.checkName = false
                                }
                                // console.log(po)
                                po.relationEventInfo.forEach(function (item) {
                                    // console.log(item)
                                    item.boundedContext = {}
                                    item.boundedContext.name = po.boundedContext.name
                                    item.elementView = {}
                                    item.elementView.id = po.elementView.id
                                    // item.eventValue.boundedContext.name = po.boundedContext.name
                                    modelForElements["RelationEventInfo"].push(item)
                                })
                                modelForElements["Policy"].push(po);
                            })
                        }


                    })

                    //template Setting
                    if (me.template.length > 0) {
                        var template = me.template;
                    } else {
                        var template = 'spring-boot';
                    }

                    //   npm   tree       .
                    var lastIndex = me.$manifestsPerTemplate[template].length - 1


                    me.$manifestsPerTemplate[template].forEach(function (element, index) {
                        element = element.replace('./', '')
                        var doit = function (content) {

                            if (element.includes('for-model')) {
                            }
                            var test = ''
                            var count = 0
                            if (!content.includes('forEach:')) {
                                count = count + 1
                                if (element.includes('for-model')) {
                                    var forEach = 'Model'
                                    test = test.concat('forEach: ' + forEach)
                                } else {
                                    var forEach = 'BoundedContext'
                                    test = test.concat('forEach: ' + forEach)
                                }
                            } else {
                                var forEach = content.split("forEach:")[1].split("\n")[0].trim();
                            }

                            if (!content.includes('fileName:')) {
                                count = count + 1
                                if (element.split('/')) {
                                    var fileName = element.substring(element.lastIndexOf("/") + 1);
                                    test = test.concat('\n')
                                    test = test.concat('fileName: ' + fileName)
                                }
                            }

                            if (!content.includes('path:')) {
                                var pathData = element.substring(0, element.lastIndexOf("/") + 1);
                                count = count + 1
                                if (element.includes('for-model')) {
                                    test = test.concat('\n')
                                    test = test.concat(`path: ${path.dirname(element).replace('./', '')}`)
                                } else {
                                    test = test.concat('\n')
                                    test = test.concat(`path: {{name}}/${path.dirname(element).replace('./', '')}`)
                                }
                            }

                            if (count == 3) {
                                test = test.concat('\n---\n')
                            } else {
                                test = test.concat('\n')
                            }

                            if (test.length > 0) {
                                content = test.concat(content)
                            }

                            var model = modelForElements[forEach];

                            // if(model[0]._type == 'org.uengine.modeling.model.Policy' && model[0].relationEventInfo.length > 0)

                            var process = function (ele) {
                                if (ele == undefined) {
                                    var optionsMetadataAndSource = content.split("---"); //  
                                    var optionsPathHeader = yamlpaser.load(optionsMetadataAndSource[0])

                                    var elementPath = element.split('/');
                                    var elementPathString = elementPath[0] + '/' + elementPath[1];
                                    if (!optionsPathHeader['path'].includes(elementPathString)) {
                                        optionsPathHeader['path'] = elementPathString.concat(optionsPathHeader['path'])
                                    }

                                    var optionsPath = optionsPathHeader['path'].split('/');
                                    var optionsPathString = '';
                                    for (var o = 1; o < optionsPath.length; o++) {
                                        if (o == 1) {
                                            optionsPathString = optionsPathString.concat(optionsPath[o]);
                                        } else {
                                            optionsPathString = optionsPathString.concat('/' + optionsPath[o]);
                                        }
                                    }
                                    //gateway2/src/main/java/com/example/template
                                    //gateway/src/main/java/com/example

                                    // FullPath : for-model/gateway/src/main/java/com/example/template/Application.java
                                    // Path: /com/example
                                    // if()
                                    var options = {
                                        "package": `${optionsPathString}`.replace(elementPath[1] + '/src/main/java/', '').replace(/\//gi, '.'),
                                        "packagePath": `${optionsPathString}`
                                    }

                                    ele = me.model

                                } else {
                                    var options = {
                                        "package": `${me.projectName}`,
                                        "packagePath": `src/main/java/${me.projectName.replace(".", "/")}`
                                    }
                                }

                                ele.options = options;

                                var gen = window.$Mustache.render(content, ele);
                                var metadataAndSource = gen.split("---"); //  
                                var header = yamlpaser.load(metadataAndSource[0])
                                var options = {}
                                Object.keys(header).forEach(function (keyValue, idx) {
                                    var key = keyValue
                                    var value = Object.values(header)[idx]
                                    if (key == 'path' && (value.lastIndexOf('.') == value.length - 1)) {
                                        value = value.substring(0, value.lastIndexOf(".") - 1)
                                    }
                                    options[key] = value;
                                })

                                var fileName = options['fileName']
                                var fullPath = options['path'] + '/' + options['fileName']
                                // console.log(fullPath)
                                if (fileName.includes('Application'))

                                    if (elementPathString && !fullPath.includes(elementPathString)) {
                                        fullPath = elementPathString.concat(fullPath)
                                    }
                                // /src/main/java/com/example/Application.java
                                // for-model/gateway2/src/main/java/com/example/template/Application.java
                                var source = ''

                                if (metadataAndSource.length > 1) {
                                    for (var data in metadataAndSource) {

                                        if (data != 0 && data < metadataAndSource.length - 1) {
                                            source = source.concat(metadataAndSource[data] + '---\n')
                                        } else if (data == metadataAndSource.length - 1) {
                                            source = source.concat(metadataAndSource[data])
                                        }
                                    }
                                }

                                var codeValue = {}
                                var fullValue = {}
                                source = source.replace('\n', '')
                                if (source.length > 1) {
                                    if (ele._type == 'org.uengine.modeling.model.Model') {
                                        // /src/main/java/com/example/Application.java
                                        // gateway2/src/main/java/com/example/template/Application.java

                                        codeValue = {
                                            'key': elementPath[1] + fileName,
                                            'fileName': fileName,
                                            'code': source,
                                            'file': me.fileType(fileName)
                                        }

                                        fullValue = {
                                            'key': elementPath[1] + fileName,
                                            'fullPath': fullPath.replace('for-model/', '')
                                        }
                                    } else {
                                        codeValue = {
                                            'key': ele.elementView.id + fileName,
                                            'fileName': fileName,
                                            'code': source,
                                            'file': me.fileType(fileName)
                                        }

                                        fullValue = {
                                            'key': ele.elementView.id + fileName,
                                            'fullPath': fullPath
                                        }
                                    }


                                    me.getCodeList.push(codeValue)
                                    me.fullPathList.push(fullValue)
                                }
                            }


                            if (Array.isArray(model)) {
                                model.forEach(process);
                            } else {
                                process(model);
                            }

                        } //end of doit


                        var xhttp = new XMLHttpRequest();
                        xhttp.onreadystatechange = function () {

                            if (this.readyState == 4 && this.status == 200) {
                                if (me.template == 'GIT') {
                                    var gitCodeObj = JSON.parse(this.responseText);
                                    var gitCodeTmp = Base64.decode(gitCodeObj.content)
                                    doit(gitCodeTmp)
                                } else {
                                    doit(this.responseText);
                                }
                                if (lastIndex == index) {
                                    resolve()
                                }
                            }
                        };
                        if (me.template == 'GIT') {
                            xhttp.open("GET", me.gitCodeUrl[element], true);
                            xhttp.send();
                        } else {
                            xhttp.open("GET", "/static/templates/" + template + "/" + element, true);
                            xhttp.send();
                        }


                    });

                })

            },
            fileType(file) {

                var type;

                if (file.includes('.java')) {
                    type = 'java'
                } else if (file.includes('Dockerfile')) {
                    type = 'docker'
                } else if (file.includes('.xml')) {
                    type = 'xml'
                } else if (file.includes('.yaml') || file.includes('.yml') || file.includes('.properties') || file.includes('mvnw') || file.includes('.groovy')) {
                    type = 'txt'
                } else if (file.includes('md')) {
                    type = 'md'
                } else if (file.includes('.jpg') || file.includes('.png') || file.includes('.jpeg')) {
                    type = 'png'
                } else if (file.includes('.json')) {
                    type = 'json'
                } else if (file.includes('.py')) {
                    type = 'python'
                }

                return type
            },
            findFile(key, name) {
                var me = this
                var ob = {}

                me.getCodeList.some(function (meta) {
                    if (meta.key == key && meta.fileName == name) {
                        ob = {
                            key: meta.key,
                            code: meta.code,
                            file: meta.file,
                        }
                    }
                })
                if (ob.key == undefined) {
                    ob = {
                        key: name,
                    }
                }

                return ob
            },
