            bindEvents: function (opengraph) {
                var me = this;
                var el = me.$el;
                var canvasEl = $(opengraph.container);
                if (!canvasEl || !canvasEl.length) {
                    return;
                }
                this.canvas = opengraph.canvas;
                //    
                $(el).find('.draggable').draggable({
                    start: function () {
                        canvasEl.data('DRAG_SHAPE', {
                            'component': $(this).attr('_component'),
                            'width': $(this).attr('_width'),
                            'height': $(this).attr('_height')
                        });
                    },
                    helper: 'clone',
                    appendTo: canvasEl
                });

                canvasEl.droppable({
                    drop: function (event, ui) {
                        var componentInfo = canvasEl.data('DRAG_SHAPE'),
                            shape, element;
                        if (componentInfo) {
                            var dropX = event.pageX - canvasEl.offset().left + canvasEl[0].scrollLeft;
                            var dropY = event.pageY - canvasEl.offset().top + canvasEl[0].scrollTop;

                            dropX = dropX / opengraph.scale;
                            dropY = dropY / opengraph.scale;

                            componentInfo = {
                                component: componentInfo.component,
                                x: dropX,
                                y: dropY,
                                width: parseInt(componentInfo.width, 10),
                                height: parseInt(componentInfo.height, 10),
                                label: ''
                            }
                            me.addElement(componentInfo);
                        }
                        canvasEl.removeData('DRAG_SHAPE');
                    }
                });
            },
            uuid: function () {
                function s4() {
                    return Math.floor((1 + Math.random()) * 0x10000)
                        .toString(16)
                        .substring(1);
                }

                return s4() + s4() + '-' + s4() + '-' + s4() + '-' +
                    s4() + '-' + s4() + s4() + s4();
            },
            onConnectShape: function (edge, from, to) {

                var me = this;
                //   ( ),     from, to      .
                //=>  .
                //    
                var edgeElement, originalData;
                var isComponent = false;
                if (edge.shape) {
                    edgeElement = edge;
                } else {
                    isComponent = true;
                    edgeElement = edge.element;
                }
                // console.log(from, to)

                if (edgeElement && from && to) {
                    var vertices = '[' + edgeElement.shape.geom.vertices.toString() + ']';
                    var componentInfo = {
                        component: 'class-relation',
                        sourceElement: from.$parent,
                        targetElement: to.$parent,
                        vertices: vertices,
                        isFilled: true,
                        relationView: {
                            style: JSON.stringify({}),
                            value: vertices,
                        }
                    }

                    from.$parent.value.elementView.id = from.id;
                    to.$parent.value.elementView.id = to.id;

                    if (isComponent) {
                        me.canvas.removeShape(edgeElement, true);
                        //this.removeComponentByOpenGraphComponentId(edgeElement.id);
                        //    originalData   
                        this.addElement(componentInfo)
                    } else {
                        me.canvas.removeShape(edgeElement, true);
                        //     
                        this.addElement(componentInfo);
                    }
                    // this.syncOthers();
                }
            },
            makeElement(component, ele, check) {
                var me = this;
                if (check) {
                    var element = component.computed.createNew(
                        ele.elementView.id,
                        ele.elementView.x,
                        ele.elementView.y,
                        ele.elementView.width,
                        ele.elementView.height
                    )

                    element.name = "tmp";
                    element.loadData = true;
                    if (component.name == 'bounded-context-definition') {
                        element.aggregates = ele.aggregates
                        element.policies = ele.policies
                    } else if (component.name == 'aggregate-definition') {
                        element.aggregateRoot = ele.aggregateRoot
                        element.boundedContext = ele.boundedContext
                        if (ele.events) {
                            element.events = ele.events
                        } else {
                            element.events = []
                        }
                        if (ele.commands) {
                            element.commands = ele.commands
                        } else {
                            element.commands = []
                        }
                        if (ele.viewes) {
                            element.viewes = ele.viewes
                        } else {
                            element.viewes = []
                        }
                    } else if (component.name == 'domain-event-definition') {
                        if (ele.aggregate) {
                            ele.aggregate.namePascalCase = changeCase.pascalCase(ele.aggregate.name)
                            ele.aggregate.nameCamelCase = changeCase.camelCase(ele.aggregate.name)
                            // ele.aggregate.namePlural = pluralize(ele.aggregate.name);
                        }
                        element.aggregate = ele.aggregate
                        element.boundedContext = ele.boundedContext
                        element.eventToPolicy = ele.eventToPolicy
                        element.fieldDescriptors = ele.fieldDescriptors
                        element.trigger = ele.trigger
                    } else if (component.name == 'command-definition') {
                        ele.aggregate.namePascalCase = changeCase.pascalCase(ele.aggregate.name)
                        ele.aggregate.nameCamelCase = changeCase.camelCase(ele.aggregate.name)
                        ele.aggregate.namePlural = changeCase.camelCase(pluralize(ele.aggregate.name));

                        element.aggregate = ele.aggregate
                        element.boundedContext = ele.boundedContext
                        element.restfulType = ele.restfulType
                    } else if (component.name == 'policy-definition') {
                        ele.aggregate.namePascalCase = changeCase.pascalCase(ele.aggregate.name)
                        ele.aggregate.nameCamelCase = changeCase.camelCase(ele.aggregate.name)
                        ele.aggregate.namePlural = changeCase.camelCase(pluralize(ele.aggregate.name));

                        element.aggregate = ele.aggregate
                        element.boundedContext = ele.boundedContext
                        element.eventToPolicy = ele.eventToPolicy
                        element.relationEventInfo = ele.relationEventInfo
                    } else if (component.name == 'view-definition') {
                        ele.aggregate.namePascalCase = changeCase.pascalCase(ele.aggregate.name)
                        ele.aggregate.nameCamelCase = changeCase.camelCase(ele.aggregate.name)
                        ele.aggregate.namePlural = changeCase.camelCase(pluralize(ele.aggregate.name));

                        element.aggregate = ele.aggregate
                        element.boundedContext = ele.boundedContext
                        element.dataProjection = ele.dataProjection
                        element.aggregateEvents = ele.aggregateEvents

                        if (ele.dataProjection == 'cqrs') {
                            console.log(ele)
                            element.viewFieldDescriptors = ele.viewFieldDescriptors
                        } else {
                            // element.fieldDescriptorsMash = ele.viewFieldDescriptors
                        }
                    }

                    // element.portGenerated = ele.portGenerated
                    me.value['definition'].push(element);
                } else {
                    var element = component.computed.createNew(
                        ele.sourceElement,
                        ele.targetElement,
                        ele.relationView.value,
                    )
                    // element.name = ele.name
                    element.relationKey = ele.relationKey
                    element.relationValue = ele.relationValue
                    me.value['relation'].push(element);
                }

                element.drawer = true;

                me.$nextTick(function () {
                    element.name = ele.name;
                    element.drawer = false;
                })

                return element
            },
            loadAddElement: function (value) {
                var me = this;

                // // console.log(JSON.parse(JSON.stringify(value)))
                me.value.definition = [];
                me.value.relation = [];
                me.projectName = value.projectName
                var boundedComponent = me.getComponentByName('bounded-context-definition');
                var aggregateComponent = me.getComponentByName('aggregate-definition');
                var commandComponent = me.getComponentByName('command-definition');
                var eventComponent = me.getComponentByName('domain-event-definition');
                var policyComponent = me.getComponentByName('policy-definition');
                var viewComponent = me.getComponentByName('view-definition');
                var relationComponent = me.getComponentByName('class-relation');

                if (value.boundedContexts)
                    value.boundedContexts.forEach(function (bounded, boundedIdx) {
                        // // console.log(bounded)
                        if (value.boundedContexts[boundedIdx].elementView.id == bounded.elementView.id) {
                            var ele = me.makeElement(boundedComponent, bounded, true)
                            value.boundedContexts[boundedIdx] = ele
                            bounded.aggregates.forEach(function (agg, aggIdx) {
                                // // console.log(agg)

                                if (bounded.aggregates[aggIdx].elementView.id == agg.elementView.id) {
                                    var aggEle = me.makeElement(aggregateComponent, agg, true)
                                    bounded.aggregates[aggIdx] = aggEle
                                }
                                if (agg.events) {
                                    agg.events.forEach(function (ev, idx) {
                                        if (agg.events[idx].elementView.id == ev.elementView.id) {
                                            var ele = me.makeElement(eventComponent, ev, true)
                                            agg.events[idx] = ele
                                        }
                                    })
                                }
                                if (agg.commands) {
                                    agg.commands.forEach(function (co, idx) {
                                        if (agg.commands[idx].elementView.id == co.elementView.id) {
                                            var ele = me.makeElement(commandComponent, co, true)
                                            agg.commands[idx] = ele
                                        }
                                    })
                                }

                                if (agg.viewes) {
                                    agg.viewes.forEach(function (vi, idx) {
                                        if (agg.viewes[idx].elementView.id == vi.elementView.id) {
                                            var ele = me.makeElement(viewComponent, vi, true)
                                            agg.viewes[idx] = ele
                                        }
                                    })
                                }
                            })
                            if (bounded.policies) {
                                bounded.policies.forEach(function (po, idx) {
                                    if (bounded.policies[idx].elementView.id == po.elementView.id) {
                                        var ele = me.makeElement(policyComponent, po, true)
                                        bounded.policies[idx] = ele
                                    }

                                })
                            }
                        }
                    })

                if (value.relations)
                    value.relations.forEach(function (line) {
                        me.makeElement(relationComponent, line, false)
                    })

            },
            addElement: function (componentInfo, bounded) {
                this.enableHistoryAdd = true;
                var me = this;
                var additionalData = {};
                var vueComponent = me.getComponentByName(componentInfo.component);
                var element;
                console.log(bounded)
                if (componentInfo.component == 'class-relation') {
                    element = vueComponent.computed.createNew(
                        this.uuid(),
                        componentInfo.sourceElement.value,
                        componentInfo.targetElement.value,
                        componentInfo.vertices,
                    );
                } else {
                    element = vueComponent.computed.createNew(
                        this.uuid(),
                        componentInfo.x,
                        componentInfo.y,
                        componentInfo.width,
                        componentInfo.height
                    );
                }
                if (me.value == null) {
                    me.value = {
                        'definition': [],
                        'relation': []
                    }
                }
                if (element._type == 'org.uengine.modeling.model.Relation') {
                    me.value['relation'].push(element);
                    this.$EventBus.$emit('storage')

                } else {
                    if (bounded != undefined) {
                        if (bounded) {
                            console.log("bounded")
                            return element
                        }
                    } else {
                        me.value['definition'].push(element);
                        this.$EventBus.$emit('storage')

                    }
                }
            },
            getComponentByName: function (name) {
                var componentByName;
                $.each(window.Vue._components, function (i, component) {
                    if (component.name == name) {
                        componentByName = component;
                    }
                });
                return componentByName;
            },
            getComponentByClassName: function (className) {
                var componentByClassName;

                $.each(window.Vue.classModelingComponents, function (i, component) {
                    if (component.default.computed && component.default.computed.className && component.default.computed.className() == className) {
                        componentByClassName = component.default;
                    }
                });
                return componentByClassName;
            },
            recursiveDeepCopy(o) {
                var me = this
                var newO,
                    i;

                if (typeof o !== 'object') {
                    return o;
                }
                if (!o) {
                    return o;
                }

                if ('[object Array]' === Object.prototype.toString.apply(o)) {
                    newO = [];
                    for (i = 0; i < o.length; i += 1) {
                        newO[i] = me.recursiveDeepCopy(o[i]);
                    }
                    return newO;
                }

                newO = {};
                for (i in o) {
                    if (o.hasOwnProperty(i)) {
                        newO[i] = me.recursiveDeepCopy(o[i]);
                    }
                }
                return newO;
            }
        }
    }
</script>

