<script>
    export default {
        name: 'ViewUpdate',
        props: {
            value: Object,
            updateItem: Object,
            events: Array,
            index: Number,
        },
        data: function () {
            return {
                // selectEvent: {},
                // viewField: {},
                // eventField: {},
                // conditionViewField: {},
                // conditionEventField: {}
            }
        },
        methods: {
            removeMainRow(viewId) {
                // console.log(val, viewId)
                // var obj = {
                //     state: 'deleteUpdateFieldDescriptors',
                //     key: val.key
                // }
                // this.$EventBus.$emit(`${viewId}`, obj)
                var me = this

                me.value.viewFieldDescriptors.updateFieldDescriptors[viewId] = null
                me.value.viewFieldDescriptors.updateFieldDescriptors = me.value.viewFieldDescriptors.updateFieldDescriptors.filter(n => n)
            },
            removeRow: function (table, removeKey) {
                var me = this
                table.some(function (views, idx) {
                    if (idx == removeKey) {
                        if (idx > -1) {
                            table.splice(idx, 1)
                        }
                    }
                })
            },
            addRow: function (table) {
                var me = this

                if ( table.length !=0 && (Object.keys(table[table.length - 1].viewField).length === 0 || Object.keys(table[table.length - 1].eventField).length === 0) ) {
                    var designer = this.getComponent('modeling-designer')
                    designer.text = "TextField input Text"
                    designer.snackbar = true
                } else {
                    var obj = {
                        viewField: {},
                        eventField: {}
                    }
                    table.push(obj)
                }

            },
            removeConditionRow: function (table, removeKey) {
                var me = this
                table.some(function (views, idx) {
                    if (idx == removeKey) {
                        if (idx > -1) {
                            table.splice(idx, 1)
                        }
                    }
                })
            },
            addConditionRow: function (table) {
                var me = this



                if (table.length !=0 && ( Object.keys(table[table.length - 1].conditionViewField).length === 0 || Object.keys(table[table.length - 1].conditionEventField).length === 0) ) {
                    var designer = this.getComponent('modeling-designer')
                    designer.text = "TextField input Text"
                    designer.snackbar = true
                } else {
                    var obj = {
                        conditionViewField: {},
                        conditionEventField: {}
                    }
                    table.push(obj)
                }

                //
                // var obj = {
                //     conditionViewField: {},
                //     conditionEventField: {}
                // }
                // table.push(obj)

            },
            getComponent(componentName) {
                let component = null
                let parent = this.$parent
                while (parent && !component) {
                    if (parent.$options.name === componentName) {
                        component = parent
                    }
                    parent = parent.$parent
                }
                return component
            },
        }
    }
</script>